<template>
  <div>
      <div class="float">
        <div class="float-wrapper clearFix">
          <div class="float-main">
            <h1 class="name">{{seller.name}}</h1>
            <star-component :size="48" :seller="seller" class="star-wrapper"></star-component>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li v-for="this(item,index) in seller.supports" class="bulletin">
                <span class="icon" :class="iconMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="passage">
              <p class="content">
                {{seller.bulletin}}
              </p>
            </div>
          </div>
        </div>
        <div class="float-close" @click="hide">
          <i class="fa fa-close fa-3x"></i>
        </div>
      </div>
  </div>
</template>

<script>
  import star from './contentdetail/tools/star/star'
  export default{
    data () {
      return {
        iconMap: ['decrease', 'discount', 'guarantee', 'invoice', 'special']
      }
    },
    props: {
      seller: Object
    },
    components: {
      starComponent: star
    },
    methods: {
      hide () {
        this.$emit('hideFloat')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../common/stylus/base.styl'
  @import '../common/stylus/mixin.styl'
  .float
    position fixed
    z-index 101
    top 0
    left 0
    width 100%
    height 100%
    overflow auto
    background-color rgba(7,17,27,.8)
    .float-wrapper
      min-height 100%
      width 100%
      .float-main
        margin-top 64px
        padding-bottom 64px
        color white
        text-align center
        .star-wrapper
          padding 2px 0
          margin-top 18px
        .name
          font-size 16px
          font-weight 700
          line-height 16px
        .title
          display flex
          width 80%
          margin 30px auto 24px auto
          .line
            flex 1
            top -6px
            position relative
            border-bottom 1px solid rgba(255,255,255,.2)
          .text
            padding 0 12px
            font-size 14px
            font-weight 700
            line-height 14px
        .supports
          text-align left
          width 80%
          margin 0 auto
          font-size 0
          .bulletin
            margin-bottom 12px
            padding 0 12px
            &:last-child
              margin-bottom 0
            .icon
              display inline-block
              width 16px
              height 16px
              margin-right 6px
              background-size 16px
              background-repeat no-repeat
              vertical-align top
              &.decrease
                bg-img('decrease_2')
              &.discount
                bg-img('discount_2')
              &.guarantee
                bg-img('guarantee_2')
              &.invoice
                bg-img('invoice_2')
              &.special
                bg-img('special_2')


            .text
              font-size 12px
              font-weight 200
              color white
              line-height 16px



        .passage
          width 80%
          margin 0 auto
          .content
            padding 0px 12px
            margin-top 24px
            font-size 12px
            font-weight 200
            line-height 24px
            text-align start


    .float-close
      position relative
      width 32px
      height 32px
      margin -64px auto 0 auto
      clear both font-size 32px
      .fa-close
        color white

</style>



