<template>
  <div>
    <div class="header">
      <div class="content-wrapper">
        <div class="avatar">
          <img :src="seller.avatar" width="64" height="64">
        </div>
        <div class="describe">
          <div class="title">
            <span class="brand"></span>
            <span class="name">{{seller.name}}</span>
          </div>
          <div class="description">
            {{seller.description}}/{{seller.deliveryTime}}分钟送达
          </div>
          <div class="supports" v-if="seller.supports">
            <span class="icon" :class="iconMap[seller.supports[0].type]"></span>
            <span class="text">{{seller.supports[0].description}}</span>
          </div>
        </div>
        <div class="bulletin" @click="showFloat=true">
          <span class="text" v-if="seller.supports">{{seller.supports.length}}个</span>
          <span class="icon-keyboard_arrow_right"></span>
        </div>
      </div>
      <div class="bulletin-wrapper" @click="showFloat=true">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <span class="icon-keyboard_arrow_right"></span>
      </div>
      <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
      </div>
    </div>
    <transition>
      <float-component v-show="showFloat" @hideFloat="showFloat=false" :seller="seller"></float-component>
    </transition>
  </div>
</template>

<script>
  import float from './float'
  export default{
    data () {
      return {
        iconMap: ['decrease', 'discount', 'guarantee', 'invoice', 'special'],
        showFloat: false
      }
    },
    components: {
      floatComponent: float
    },
    methods: {
    },
    props: {
      seller: {
        type: Object
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../common/stylus/mixin.styl'
  .header
    overflow:hidden
    position relative
    background-color rgba(7,17,27,.5)
    .content-wrapper
      position relative
      padding 24px 12px 18px 24px
      font-size 0
      .avatar
        display inline-block
        border-radius 2px
      .describe
        vertical-align top
        margin-left 16px
        display inline-block
        .title
          margin 2px 0 8px 0
          .brand
            vertical-align top
            display inline-block
            width 30px
            height 18px
            bg-img('brand')
            background-size 30px 18px
            background-repeat no-repeat
          .name
            font-size 16px
            line-height 18px
            color: #fff
            font-weight:bold
            margin-left 6px
        .description
          font-size 12px
          color #fff
          font-weight 200
          line-height 12px
          margin-bottom 10px

        .supports
          font-size 0
          .icon
            vertical-align top
            display inline-block
            width 12px
            height 12px
            margin-right 4px
            background-size 12px 12px
            background-repeat no-repeat
            &.decrease
              bg-img('decrease_1')
            &.discount
              bg-img('discount_1')
            &.guarantee
              bg-img('guarantee_1')
            &.invoice
              bg-img('invoice_1')
            &.special
              bg-img('special_1')


          .text
            font-size 10px
            color #fff
            line-height 12px


      .bulletin
        position:absolute
        height 24px
        line-height 24px
        border-radius 14px
        padding 0 8px
        background-color rgba(0,0,0,.2)
        right 12px
        bottom 18px
        text-align center
        .text
          font-size 10px
          color #fff
          font-weight 200
          vertical-align top
        .icon-keyboard_arrow_right
          font-size 10px
          color #fff
          line-height 24px
          margin-left 2px

    .bulletin-wrapper
      white-space nowrap
      overflow hidden
      text-overflow ellipsis
      height 28px
      background-color rgba(7,17,27,0.2)
      padding 0 22px 0 12px
      line-height 28px
      position relative
      color #fff
      .bulletin-title
        width 22px
        height 12px
        display inline-block
        bg-img('bulletin')
        background-size 22px 12px
        background-repeat no-repeat
        vertical-align top
        margin-top 8px
      .bulletin-text
        vertical-align top
        margin 0 4px
        font-size 10px
        color #fff
        font-weight 200
      .icon-keyboard_arrow_right
        position absolute
        font-size 10px
        right 12px
        top 8px


    .background
      position absolute
      top 0
      left 0
      right 0
      bottom 0
      z-index -1
      filter blur(10px)
</style>



