import Vue from 'vue'
import Router from 'vue-router'
import merchant from '../components/contentdetail/merchant.vue'
import rating from '../components/contentdetail/rating.vue'
import shop from '../components/contentdetail/shop'

Vue.use(Router)
const routes = [
    // {path: '/', redirect: '/merchant'},
    {path: '/merchant', component: merchant},
    {path: '/shop', component: shop},
    {path: '/rating', component: rating}
]
export default new Router({
  linkActiveClass: 'active',
  routes
})



